/*Base*/
.ProfileContainer {
  background-color: #cfe2e2;
  width: auto;
  height: 90vh;
  text-align: center;
  border: 65px inset darkslategrey;
  overflow: hidden;
}
.TitleContainer {
  width: 100%;
  height: 99.5vh;
  /* backgroundimage: url(${Background}); */
  display: flex;
  align-items: center;
  position: relative;
}
.TitleText {
  width: 500px;
  background: black;
  fontfamily: apple system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  background: transparent;
  cursor: default;
  display: flex;
  position: absolute;
  top: 46%;
  left: 27%;
}

#About_Me {
  order: 2;
  text-align: justify;
  margin-left: 1.4%;
  width: 80vw;
}

#Details {
  order: 4;
  width: 80vw;
}
#ProfilePic {
  order: 3;
  /* position: absolute; */
  /* left:50%; */
  margin-top: 1%;
  margin-left: 5vw;
  margin-right: 5vw;
}
#Title {
  textalign: 'center';
}

.Information_Box {
  margin: 1%;
  background-color: darkgrey;
  border-radius: 4%;
  border: 3px solid black;
  width: 25vw;
  height: 44vh;
  overflow: auto;
  -webkit-border-radius: 4%;
  -moz-border-radius: 4%;
  -ms-border-radius: 4%;
  -o-border-radius: 4%;
}
